@import url(app.css);

.doctors {
  margin-bottom: 5rem;
  padding: 4rem 0;
  background: linear-gradient(
    135deg,
    var(--bg-light) 0%,
    rgba(37, 99, 235, 0.02) 100%
  );
}

.doctors-card-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 2rem;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.page-heading {
  font-size: 2.5rem;
  font-weight: 800;
  font-family: var(--font-heading);
  color: var(--text-primary);
  text-align: center;
  margin-bottom: 1rem;
  background: linear-gradient(
    135deg,
    var(--text-primary) 0%,
    var(--primary-color) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.doctors-intro {
  text-align: center;
  max-width: 600px;
  margin: 0 auto 3rem;
  font-size: 1.125rem;
  line-height: 1.6;
  color: var(--text-secondary);
}

@media (max-width: 768px) {
  .doctors {
    padding: 2rem 0;
  }

  .doctors-card-container {
    grid-template-columns: 1fr;
    gap: 1.5rem;
    padding: 0 1rem;
  }

  .page-heading {
    font-size: 2rem;
  }

  .doctors-intro {
    font-size: 1rem;
    margin-bottom: 2rem;
  }
}

@media (max-width: 480px) {
  .doctors-card-container {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
}
